.catalog__card-title--more {
  margin: 0 0 26px;

  font-size: 16px;
  line-height: 20px;
  font-family: $ff-oswald;
  text-align: center;
  color: #111111;
  text-transform: uppercase;
}

.catalog__text--more {
  margin: 0 0 21px;
}

.button--more {
  color: #444444;

  background-color: #f2f2f2;

  &:hover {
    background-color: #ebebeb;
  }

  &:focus {
    background-color: #969090;
  }
}
