@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../../fonts/lato/Lato-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswald/oswaldmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Lato", sans-serif;
  color: #444444;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  object-fit: cover;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.page-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #ffffff;
}

.main {
  flex-grow: 1;
}

.button {
  display: inline-block;
  padding: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}

.button:hover {
  background-color: #5eaa2f;
}

.button:focus {
  background-color: #5eaa2f;
}

.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
  opacity: 0.3;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}

.page-title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", sans-serif;
  color: #000000;
}

.main-footer__logo-html {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  line-height: 20px;
  color: #444444;
  text-decoration: none;
}
.main-footer__logo-html svg {
  width: 27px;
  height: 34px;
}

.logo-html__text {
  color: #444444;
}

.site-list__link {
  display: block;
  padding: 23px 23px 22px;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

.nav__toggle {
  position: absolute;
  top: 9px;
  right: 0;
  width: 24px;
  height: 24px;
  margin: 0;
  padding: 4px 0;
}

.nav--close .nav__list {
  display: none;
}

.nav__list {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  padding: 0 20px 20px 22px;
  list-style: none;
  background-color: #ffffff;
}

.main-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.logo {
  display: block;
  width: fit-content;
  cursor: pointer;
}
.logo--full {
  display: grid;
  align-items: center;
  column-gap: 56px;
  grid-template-columns: auto 1fr;
}

.logo__cat-img {
  display: block;
  width: 33px;
  height: 38px;
}

.logo__cat-text {
  display: block;
  width: 101px;
  height: 18px;
}

.promo__description {
  margin: 0 -20px;
  color: #ffffff;
  background-color: rgba(104, 183, 56, 0.85);
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-background-mobile.jpg");
  background-repeat: repeat, no-repeat;
  background-position: top;
  background-size: auto, cover;
}

.promo__button {
  width: 100%;
}

.promo__title {
  margin-bottom: 25px;
  font-family: "Oswald", sans-serif;
  color: inherit;
}

.promo__text {
  margin: 0;
  font-size: 14px;
  line-height: 14px;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

.offer__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.offer__item {
  padding: 30px 20px 18px;
  background-color: #f2f2f2;
}

.offer__title {
  margin: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  font-family: "Oswald", sans-serif;
  color: #000000;
  text-transform: uppercase;
}

.offer__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
}

.offer__link {
  font-size: 16px;
  line-height: 16px;
  font-family: "Oswald", sans-serif;
  color: #000000;
  text-decoration: none;
}

.technology__title {
  margin-bottom: 40px;
}

.technology__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 20px;
  grid-template-columns: 1fr;
}

.technology__item {
  position: relative;
  min-height: 60px;
  padding: 0 0 0 80px;
  font-size: 14px;
  line-height: 18px;
}

.slider {
  position: relative;
  width: 100%;
  max-width: 280px;
  margin: 0 auto;
}

.slider__container {
  position: relative;
  min-height: 256px;
  overflow: hidden;
}

.slider__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.slider__item img {
  display: block;
  width: 100%;
  height: 100%;
}

.slider__item--before {
  z-index: 2;
}

.slider__item--after {
  right: 0;
  left: auto;
  z-index: 1;
}

.slider__item--before img {
  object-position: left;
}

.slider__item--after img {
  object-position: right;
}

.example {
  background-color: #eaeaea;
}

.example__title {
  margin-bottom: 40px;
}

.example__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
}

.example__result-cover {
  display: grid;
  gap: 32px;
  grid-template-columns: repeat(2, 124px);
}

.example__result-item {
  padding: 14px 10px 13px;
  text-align: center;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
}

.example__result-value {
  font-size: 24px;
  line-height: 24px;
  font-family: "Oswald", sans-serif;
  color: #000000;
  text-transform: uppercase;
}

.example__result-description {
  display: block;
  margin: 8px 8px -29px;
  padding: 0 5px;
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
  background-color: #eaeaea;
}

.example__price {
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
}

.application__container {
  width: 100%;
  max-width: 1260px;
  padding: 27px 20px 39px;
}

.application__title {
  margin: 0 0 37px;
  padding: 0;
  font-size: 36px;
  line-height: 36px;
  font-family: "Oswald", sans-serif;
}

.application__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

.application__wrapper {
  width: 100%;
  max-width: 1260px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0 20px 32px;
}

.form__list {
  margin: 0;
  padding: 0;
}

.form__group {
  border: none;
}

.form__item {
  display: flex;
  margin-bottom: 14px;
  column-gap: 13px;
}

.form__title {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 26px;
  font-family: "Oswald", sans-serif;
  color: #000000;
}

.control__item {
  display: flex;
  flex-direction: row-reverse;
}

.form__label {
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", sans-serif;
  color: #444444;
  text-transform: uppercase;
}

.form__group-data {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px;
  padding: 0;
}

.form__group-contacts {
  margin: 0 0 10px;
  padding: 0;
}

.form__title-contacts {
  margin-bottom: 35px;
}

.form__group-comments {
  margin: 0 0 23px;
  padding: 0;
}

.form__title-comments {
  margin-bottom: 38px;
}

.form__group-check {
  margin: 23px 0 34px;
  padding: 0;
}

.form__list-check {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  margin-top: 34px;
  gap: 13px;
}

.control__input-check {
  width: 30px;
  height: 30px;
  margin: 0 15px 0 0;
}

.form__group-radio {
  margin-bottom: 45px;
  padding: 0;
}

.form__list-radio {
  display: flex;
  flex-direction: column;
  align-items: start;
  border: 1px solid #e7e7e7;
  gap: 25px;
}

.control__item:hover {
  color: #222222;
}
.control__item:focus {
  color: #222222;
  opacity: 0.3;
}

.form__input {
  width: 100%;
  height: 45px;
  padding: 14px 15px 13px;
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", sans-serif;
  color: #444444;
  text-transform: uppercase;
  background-color: #ffffff;
  border: 1px solid #e7e7e7;
  appearance: none;
}

.form__input:hover {
  border-color: #cdcdcd;
}
.form__input:focus {
  border-color: #444444;
  outline: none;
}
.form__input--error {
  border-color: #ff8282;
}

.form__label {
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", sans-serif;
  color: #444444;
  text-transform: uppercase;
}

.catalog__container {
  padding: 27px 20px;
}

.catalog__title {
  margin: 0;
  margin-bottom: 41px;
  padding: 0;
  font-size: 36px;
  line-height: 36px;
  font-family: "Oswald", sans-serif;
  color: #000000;
}

.catalog__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 25px;
}

.catalog__item {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
}

.catalog__link-title {
  display: block;
  margin: 0;
  text-decoration: none;
}

.catalog__card-title {
  margin: 0 0 23px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #222222;
  text-transform: uppercase;
}

.catalog__table {
  display: flex;
  flex-direction: column;
}

.catalog__table-item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 10px;
  border-bottom: 1px solid #cdcdcd;
}

.catalog__card-title--more {
  margin: 0 0 26px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #111111;
  text-transform: uppercase;
}

.catalog__text--more {
  margin: 0 0 21px;
}

.button--more {
  color: #444444;
  background-color: #f2f2f2;
}
.button--more:hover {
  background-color: #ebebeb;
}
.button--more:focus {
  background-color: #969090;
}

.supplementary__container {
  display: flex;
  flex-direction: column;
}

.supplementary__list {
  display: flex;
  flex-direction: column;
  padding: 0;
}

.supplementary__item {
  display: flex;
  flex-direction: column;
  padding: 13px 0 19px;
}

.supplementary__title-main {
  margin: 26px 0 36px;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
}

.supplementary__title {
  margin: 0;
  font-size: 16px;
  line-height: 16px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #222222;
  text-transform: uppercase;
}

.supplementary__proposal {
  min-width: 245px;
  min-height: 288px;
  padding: 185px 42px 43px;
  background-color: rgba(104, 183, 56, 0.85);
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/cat-in-box.jpg");
  background-repeat: repeat, no-repeat;
  background-position: top;
  background-size: auto, cover;
}

.supplementary__text {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
}

.map {
  position: relative;
}

.map--home {
  background-color: #ffffff;
}

.map__contact {
  display: grid;
  padding: 26px 0;
  background-color: #ffffff;
  grid-template-columns: 1fr 1fr;
}
.map__contact--grey {
  background-color: #f2f2f2;
}

.map__title {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  color: #111111;
  text-transform: uppercase;
}

.map__address {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
}

.main-footer__social {
  width: 100%;
  margin-bottom: 20px;
  padding: 20px 0 19px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.social {
  display: grid;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 24px;
  grid-template-columns: repeat(3, auto);
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 22px;
  color: #666666;
  fill: currentColor;
}

.main-footer {
  background-color: #f2f2f2;
}

.main-footer__container {
  padding-top: 40px;
  padding-bottom: 40px;
}

.main-footer__social {
  width: 100%;
  margin-bottom: 20px;
  padding: 20px 0 19px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}